import { RedactoConsentManager } from "./ConsentManager";
import { RedactoInstance } from "./types";

// Create the global Redacto instance
const redacto: RedactoInstance = new RedactoConsentManager();

// Make it available globally
if (typeof window !== "undefined") {
  (window as any).redacto = redacto;
}

// Export for module systems
export default redacto;
export { redacto };
export * from "./types";

// Auto-initialize if config is provided via data attributes
if (typeof window !== "undefined") {
  document.addEventListener("DOMContentLoaded", () => {
    const script = document.querySelector("script[data-redacto-config]");
    if (script) {
      try {
        const configAttr = script.getAttribute("data-redacto-config");
        if (configAttr) {
          const config = JSON.parse(configAttr);
          redacto.init(config);
        }
      } catch (error) {
        console.warn(
          "Failed to parse Redacto config from data attribute:",
          error
        );
      }
    }
  });
}
