import { ConsentData } from "../types";

export class CookieManager {
  private static instance: CookieManager;

  static getInstance(): CookieManager {
    if (!CookieManager.instance) {
      CookieManager.instance = new CookieManager();
    }
    return CookieManager.instance;
  }

  setCookie(
    name: string,
    value: string,
    days: number = 365,
    domain?: string,
    path: string = "/"
  ): void {
    try {
      const expires = new Date();
      expires.setTime(expires.getTime() + days * 24 * 60 * 60 * 1000);

      const cookieString = [
        `${name}=${encodeURIComponent(value)}`,
        `expires=${expires.toUTCString()}`,
        `path=${path}`,
        domain ? `domain=${domain}` : "",
        "SameSite=Lax",
        window.location.protocol === "https:" ? "Secure" : "",
      ]
        .filter(Boolean)
        .join("; ");

      document.cookie = cookieString;
    } catch (error) {
      console.warn("Failed to set cookie:", error);
    }
  }

  getCookie(name: string): string | null {
    try {
      const nameEQ = name + "=";
      const ca = document.cookie.split(";");

      for (let i = 0; i < ca.length; i++) {
        let c = ca[i];
        while (c.charAt(0) === " ") c = c.substring(1, c.length);
        if (c.indexOf(nameEQ) === 0) {
          return decodeURIComponent(c.substring(nameEQ.length, c.length));
        }
      }
      return null;
    } catch (error) {
      console.warn("Failed to get cookie:", error);
      return null;
    }
  }

  deleteCookie(name: string, domain?: string, path: string = "/"): void {
    try {
      const cookieString = [
        `${name}=`,
        "expires=Thu, 01 Jan 1970 00:00:00 GMT",
        `path=${path}`,
        domain ? `domain=${domain}` : "",
      ]
        .filter(Boolean)
        .join("; ");

      document.cookie = cookieString;
    } catch (error) {
      console.warn("Failed to delete cookie:", error);
    }
  }

  getAllCookies(): Record<string, string> {
    try {
      const cookies: Record<string, string> = {};
      const cookieArray = document.cookie.split(";");

      for (let cookie of cookieArray) {
        const [name, value] = cookie.trim().split("=");
        if (name && value) {
          cookies[name] = decodeURIComponent(value);
        }
      }

      return cookies;
    } catch (error) {
      console.warn("Failed to get all cookies:", error);
      return {};
    }
  }

  setConsentData(
    cookieName: string,
    consentData: ConsentData,
    expiryDays: number = 365
  ): void {
    try {
      const consentString = JSON.stringify(consentData);
      this.setCookie(cookieName, consentString, expiryDays);
    } catch (error) {
      console.warn("Failed to set consent data:", error);
    }
  }

  getConsentData(cookieName: string): ConsentData | null {
    try {
      const consentString = this.getCookie(cookieName);
      if (!consentString) return null;

      const consentData = JSON.parse(consentString) as ConsentData;

      // Validate consent data structure
      if (
        !consentData.timestamp ||
        !consentData.preferences ||
        !consentData.complianceType
      ) {
        return null;
      }

      return consentData;
    } catch (error) {
      console.warn("Failed to get consent data:", error);
      return null;
    }
  }

  clearConsentData(cookieName: string): void {
    this.deleteCookie(cookieName);
  }

  isConsentExpired(consentData: ConsentData, expiryDays: number): boolean {
    const now = Date.now();
    const consentTime = consentData.timestamp;
    const expiryTime = consentTime + expiryDays * 24 * 60 * 60 * 1000;

    return now > expiryTime;
  }

  getDomain(): string {
    try {
      return window.location.hostname;
    } catch (error) {
      return "localhost";
    }
  }

  getRootDomain(): string {
    try {
      const hostname = window.location.hostname;
      const parts = hostname.split(".");

      if (parts.length <= 2) {
        return hostname;
      }

      return parts.slice(-2).join(".");
    } catch (error) {
      return "localhost";
    }
  }
}
