export class DOMUtils {
  static createElement(
    tag: string,
    className?: string,
    id?: string
  ): HTMLElement {
    const element = document.createElement(tag);
    if (className) element.className = className;
    if (id) element.id = id;
    return element;
  }

  static createButton(
    text: string,
    className?: string,
    onClick?: () => void
  ): HTMLButtonElement {
    const button = document.createElement("button");
    button.textContent = text;
    if (className) button.className = className;
    if (onClick) button.addEventListener("click", onClick);
    return button;
  }

  static createDiv(className?: string, content?: string): HTMLDivElement {
    const div = document.createElement("div");
    if (className) div.className = className;
    if (content) div.innerHTML = content;
    return div;
  }

  static createSpan(className?: string, content?: string): HTMLSpanElement {
    const span = document.createElement("span");
    if (className) span.className = className;
    if (content) span.textContent = content;
    return span;
  }

  static createParagraph(
    className?: string,
    content?: string
  ): HTMLParagraphElement {
    const p = document.createElement("p");
    if (className) p.className = className;
    if (content) p.textContent = content;
    return p;
  }

  static createHeading(
    level: 1 | 2 | 3 | 4 | 5 | 6,
    className?: string,
    content?: string
  ): HTMLHeadingElement {
    const heading = document.createElement(`h${level}`);
    if (className) heading.className = className;
    if (content) heading.textContent = content;
    return heading;
  }

  static createLink(
    href: string,
    text: string,
    className?: string,
    target?: string
  ): HTMLAnchorElement {
    const link = document.createElement("a");
    link.href = href;
    link.textContent = text;
    if (className) link.className = className;
    if (target) link.target = target;
    return link;
  }

  static createCheckbox(
    id: string,
    checked: boolean = false,
    disabled: boolean = false
  ): HTMLInputElement {
    const checkbox = document.createElement("input");
    checkbox.type = "checkbox";
    checkbox.id = id;
    checkbox.checked = checked;
    checkbox.disabled = disabled;
    return checkbox;
  }

  static createLabel(
    forId: string,
    text: string,
    className?: string
  ): HTMLLabelElement {
    const label = document.createElement("label");
    label.htmlFor = forId;
    label.textContent = text;
    if (className) label.className = className;
    return label;
  }

  static addStyles(element: HTMLElement, styles: Record<string, string>): void {
    Object.assign(element.style, styles);
  }

  static addClass(element: HTMLElement, className: string): void {
    element.classList.add(className);
  }

  static removeClass(element: HTMLElement, className: string): void {
    element.classList.remove(className);
  }

  static toggleClass(element: HTMLElement, className: string): void {
    element.classList.toggle(className);
  }

  static hasClass(element: HTMLElement, className: string): boolean {
    return element.classList.contains(className);
  }

  static show(element: HTMLElement): void {
    element.style.display = "block";
  }

  static hide(element: HTMLElement): void {
    element.style.display = "none";
  }

  static isVisible(element: HTMLElement): boolean {
    return element.style.display !== "none" && element.offsetParent !== null;
  }

  static getElementById(id: string): HTMLElement | null {
    return document.getElementById(id);
  }

  static getElementsByClassName(className: string): HTMLCollectionOf<Element> {
    return document.getElementsByClassName(className);
  }

  static querySelector(selector: string): Element | null {
    return document.querySelector(selector);
  }

  static querySelectorAll(selector: string): NodeListOf<Element> {
    return document.querySelectorAll(selector);
  }

  static appendChild(parent: HTMLElement, child: HTMLElement): void {
    parent.appendChild(child);
  }

  static removeChild(parent: HTMLElement, child: HTMLElement): void {
    if (parent.contains(child)) {
      parent.removeChild(child);
    }
  }

  static insertBefore(
    parent: HTMLElement,
    newChild: HTMLElement,
    referenceChild: HTMLElement
  ): void {
    parent.insertBefore(newChild, referenceChild);
  }

  static replaceChild(
    parent: HTMLElement,
    newChild: HTMLElement,
    oldChild: HTMLElement
  ): void {
    parent.replaceChild(newChild, oldChild);
  }

  static addEventListener(
    element: HTMLElement,
    event: string,
    handler: EventListener
  ): void {
    element.addEventListener(event, handler);
  }

  static removeEventListener(
    element: HTMLElement,
    event: string,
    handler: EventListener
  ): void {
    element.removeEventListener(event, handler);
  }

  static createStyleSheet(css: string): HTMLStyleElement {
    const style = document.createElement("style");
    style.textContent = css;
    return style;
  }

  static injectCSS(css: string, id?: string): HTMLStyleElement {
    const style = this.createStyleSheet(css);
    if (id) style.id = id;
    document.head.appendChild(style);
    return style;
  }

  static removeStyleSheet(id: string): void {
    const style = document.getElementById(id) as HTMLStyleElement;
    if (style) {
      style.remove();
    }
  }

  static getViewportSize(): { width: number; height: number } {
    return {
      width: window.innerWidth || document.documentElement.clientWidth,
      height: window.innerHeight || document.documentElement.clientHeight,
    };
  }

  static isMobile(): boolean {
    return window.innerWidth <= 768;
  }

  static isTablet(): boolean {
    return window.innerWidth > 768 && window.innerWidth <= 1024;
  }

  static isDesktop(): boolean {
    return window.innerWidth > 1024;
  }

  static getDeviceType(): "mobile" | "tablet" | "desktop" {
    if (this.isMobile()) return "mobile";
    if (this.isTablet()) return "tablet";
    return "desktop";
  }

  static debounce<T extends (...args: any[]) => any>(
    func: T,
    wait: number
  ): (...args: Parameters<T>) => void {
    let timeout: NodeJS.Timeout;
    return (...args: Parameters<T>) => {
      clearTimeout(timeout);
      timeout = setTimeout(() => func(...args), wait);
    };
  }

  static throttle<T extends (...args: any[]) => any>(
    func: T,
    limit: number
  ): (...args: Parameters<T>) => void {
    let inThrottle: boolean;
    return (...args: Parameters<T>) => {
      if (!inThrottle) {
        func(...args);
        inThrottle = true;
        setTimeout(() => (inThrottle = false), limit);
      }
    };
  }

  static escapeHtml(text: string): string {
    const div = document.createElement("div");
    div.textContent = text;
    return div.innerHTML;
  }

  static sanitizeHtml(html: string): string {
    const div = document.createElement("div");
    div.innerHTML = html;
    return div.textContent || div.innerText || "";
  }
}
